//layout mixins - start

@mixin eltdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin eltdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin eltdfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin eltdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.eltdf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .eltdf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .eltdf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include eltdfTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .eltdf-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .65);
            opacity: 0;
            @include eltdfTransition(opacity .5s ease);
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .65);
        opacity: 0;
        @include eltdfTransition(opacity .5s ease);
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
	@include eltdfRelativeHolderLayout(top);
	margin: 18px 0 0;
	clear: both;

    @if ($list_type == 'shop') {
        margin: 26px 0 76px;
    }
	
	ul {
		@include eltdfRelativeHolderLayout(top);
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 22px;

            @include phone-portrait {
                margin: 0 15px;
            }
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: top;
				margin: 0;
				padding: 0;
                color: $default-text-color;
                font-size: 12px;
                font-weight: 400;
                font-family: $default-heading-font;

                &:after {
                    position: absolute;
                    content: '';
                    height: 1px;
                    width: 0;
                    top: 50%;
                    right: -5px;
                    background-color: $default-text-color;
                    @include eltdfTransform(translateY(-50%));
                    @include eltdfTransition(width .5s ease);
                }

                &:hover {
                    &:after {
                        width: calc(100% + 10px);
                        left: -5px;
                    }
                }
			}
			
			&.eltdf-pag-active {
				
				a {
					&:after {
                        width: calc(100% + 10px);
                    }
				}
			}

			&.eltdf-pag-first,
			&.eltdf-pag-last {
				margin: 0 2px;
                display: none;
				
				a {
					font-size: 24px;
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}

            &.eltdf-pag-prev,
            &.eltdf-pag-next {
                letter-spacing: .1em;
                text-transform: uppercase;

                @include phone-portrait {
                    display: none;
                }

                a {
                    color: #c5c5c5;

                    &:after {
                        background-color: #c5c5c5;
                        transform: none;
                        top: auto;
                        bottom: 1px;
                    }

                    &:hover {
                        &:after {
                            width: calc(100% + 63px);
                        }
                    }
                }
            }

            &.eltdf-pag-prev {
                float: left;
                margin-left: 0;

                a {

                    &:after {
                        left: -1px;
                    }
                }
            }

            &.eltdf-pag-next {
                float: right;
                margin-right: 0;

                a {

                    &:after {
                        left: auto;
                        right: -1px;
                    }
                }
            }
			
			@if ($list_type == 'shortcode') {
				
				&.eltdf-pag-prev {
					
					a {
						opacity: 0;
					}
				}
				
				&.eltdf-pag-next {
					
					a {
						opacity: 1;
					}
				}
				
			} @else if ($list_type == 'shop') {
				
				span {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0;
					padding: 0;
					color: $default-text-color;
                    font-weight: 400;

                     &:after {
                        content:'';
                        background-color:$default-text-color;
                        width: 15px;
                        height:1px;
                        position:  absolute;
                        left: 50%;
                        transform: translateX(-50%);
                        @include eltdfTransition(width .2s ease-in-out);
                        top: 50%;
                    }
				}
				
				a {
					
					&.next,
					&.prev {
						font-size: 0;
						line-height: 0;
					}
				}
			}
		}
	}
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 12px;
    line-height: 2em;
    letter-spacing: 0.14em;
    font-weight: 400;
    text-transform: uppercase;
    outline: none;
    font-family: $default-heading-font;
    box-sizing: border-box;
    @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin eltdfButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 14px 34px;
        
    } @else if ($size == 'medium') {
        padding: 14px 46px;
        
    } @else if ($size == 'large') {
        padding: 14px 66px;
        
    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 10px 26px;
    }
}


@mixin eltdfButtonTransparentColor() {
    color: $default-heading-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid $default-heading-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border-color: $default-heading-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 20px;
    padding: 20px 29px;
    font-family: inherit;
    font-size: 13px;
    line-height: 20px;
    font-weight: inherit;
    color: $default-text-color;
    background-color: transparent;
    border: 1px solid $default-heading-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: text;
    box-sizing: border-box;
    resize: none;
    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include eltdfPlaceholder {
        color: inherit;
    }
}

/* mixins styles - end */

